import { Component, OnInit } from '@angular/core';
import icCheckCircle from '@iconify/icons-ic/twotone-check-circle';
import { scaleIn400ms } from '../../../@vex/animations/scale-in.animation';
import { fadeInRight400ms } from '../../../@vex/animations/fade-in-right.animation';
import { stagger40ms } from '../../../@vex/animations/stagger.animation';
import { fadeInUp400ms } from '../../../@vex/animations/fade-in-up.animation';
import { scaleFadeIn400ms } from '../../../@vex/animations/scale-fade-in.animation';
import { Router, NavigationEnd } from '@angular/router';
import icChat from '@iconify/icons-ic/twotone-chat';
import icCall from '@iconify/icons-fa-solid/robot';
import { Icon } from '@visurel/iconify-angular';
import icEstrategy from '@iconify/icons-fa-solid/chess';
import { AimService } from 'src/app/services/aim/aim/aim.service';
import { Microservice } from 'src/app/interfaces/aim/microservice/microservice';
import { LS } from 'src/app/interfaces/static-data/LS';
import { filter } from 'rxjs/operators';

export interface MegaMenuFeature {
  icon: Icon;
  label: string;
  route: string;
  active: boolean;
}

@Component({
  selector: 'vex-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  animations: [
    scaleIn400ms,
    fadeInRight400ms,
    stagger40ms,
    fadeInUp400ms,
    scaleFadeIn400ms,
  ],
})
export class DashboardComponent implements OnInit {
  icCheckCircle = icCheckCircle;
  icChat = icChat;
  icCall = icCall;
  icEstrategy = icEstrategy;
  constantes = LS;
  cargando: boolean = false;
  listaMicroServices: Microservice[] = new Array(3);
  listaMicroServicesUser: any[] = new Array(3);

  constructor(private aimService: AimService, private router: Router) {
    console.log('entro');
    router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.ngOnInit();
      }
    });
  }

  ngOnInit(): void {}

  listMicroservices() {
    this.cargando = true;
  }

  onAccionService(e) {}

  mathInfoService(all, path) {
    let service = {};
    all.forEach((s) => {
      if (s.path === path) service = s;
    });
    return service;
  }
}
